<template>
	<view>
		<image src="../../static/logo02.png" class="logo" mode="widthFix"></image>
		<view class="logo-title">匆匆趣玩系统</view>
		<view class="logo-subtitle">Ver 2050.2</view>
		<button class="login-btn" open-type="getUserInfo" @tap="login()">登陆系统</button>
		<view class="register-container">
			没有账号？
			<text class="register" @tap="toRegister()">立即注册</text>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {

		}
	},
	methods: {
		toRegister:function(){
			uni.navigateTo({
				url:"/pages/register/register"
			})
		},
		// 登入的方法
			login:function(){
						let that=this
						uni.login({
							provider:"weixin",
							// 登入成功
							success:function(resp){
								let code=resp.code
								// 发请求到后端地址，加临时授权码
								that.ajax(that.url.login,"POST",{"code":code},function(resp){
									let permission=resp.data.permission
									uni.setStorageSync("permission",permission)
									//跳转到登陆页面
									uni.switchTab({
										url:"/pages/index/index"
									})

								})

								console.log("=======登入成功======")

							},
							// 登入失败回调
							fail:function(e){
								uni.showToast({
									icon:"none",
									title:"执行异常"
								})
							}
						})
					},
					
				//methods end	
	}
}
</script>

<style lang="less">
@import url('login.less');
</style>
